$(function() {
    const $workingTab = $('.js-working__tab-item');
    const $stepNumb = $('.js-working__step');
    const $infoBtn = $('.js-working__info');
    const $infoClose = $('.js-working__info-close');
    const $workingContainerInner = $('.js-working__container-inner');
    const $arrows = $workingContainerInner.attr('data-arrows').split(',')
    const $workingStepsBtn = $('.js-working__steps-btn');
    const $window = $(window)
    let initSlider = false
    let count = 0

    $infoBtn.on('click', function(){
        $(this).prev().fadeIn(300)
    })

    $infoClose.on('click', function() {
        $(this).closest('.js-working__info-text').fadeOut(300)
    })
    
    const options = {
        infinite: false,
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: false,
        nextArrow: `<button class="slick-next slick-arrow" aria-label="Next" type="button"><svg><use xlink:href="${$arrows[0]}"></use></svg></button>`,
        prevArrow: `<button class="slick-prev slick-arrow" aria-label="Next" type="button"><svg><use xlink:href="${$arrows[1]}"></use></svg></button>`,
        responsive: [
            {
              breakpoint: 768,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                adaptiveHeight: true,
                arrows: true,
              }
            },
          ],
    }
    
    const width = $(this).width();
    if(width < 768) {
        $workingContainerInner.slick(options)
        initSlider = true
    }

    $workingStepsBtn.on('click', function() {
        if(count === 3) return
        count += 1
        $stepNumb.text(count + 1)
        $workingContainerInner.parent().removeClass('active').eq(count).addClass('active');
        $workingTab.eq(count).addClass('active').prevAll().addClass('active after-active');
    })

    $workingTab.on('click', function() {
        const $windowWidth = $window.width()
        const $this = $(this)
        const idx = $workingTab.index($this)
        $this.siblings().removeClass('active after-active');
		$this.addClass('active').removeClass('after-active').prevAll().addClass('active after-active');
        $stepNumb.text(idx + 1)
        if(initSlider) $workingContainerInner.slick('destroy')
        if($windowWidth < 768) $workingContainerInner.eq(idx).slick(options)
        $workingContainerInner.parent().removeClass('active').eq(idx).addClass('active');
        count = idx
    })
})