$(function() {
    const $tab = $('.js-tab')
    const $slider = $('.our-work__slider');
    const arrows = $slider.attr('data-btn').split(',')
    const $sliderHtml = $slider.children();
    const $empty = $('.js-empty');
    const params = {
        slidesToShow: 3,
        slidesToScroll: 1,
        infinite: false,
        prevArrow:`<button class="slick-prev slick-arrow"><svg><use xlink:href="${arrows[0]}"></use></svg></button>`,
        nextArrow:`<button class="slick-next slick-arrow"><svg><use xlink:href="${arrows[1]}"></use></svg></button>`,
        responsive: [
            {
              breakpoint: 1000,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
              }
            },

            {
                breakpoint: 768,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                  dots: true,
                  mobileFirst: true,
                }
              },
        ],

    }

    $slider.slick(params)
    let cloneElements = []
    $tab.on('click', function(e) {
        const $this = $(this)
        $tab.removeClass('active')
        $this.addClass('active')
        const value = $this.attr('data-tab').toLowerCase();
        $empty.fadeOut()
        if(value === "all")  cloneElements = $sliderHtml
        else {
            $sliderHtml.each(function(i, item){
                if(item.dataset.category === value) cloneElements.push(item)
            })
        }
        if(!cloneElements.length) $empty.fadeIn()
        $slider.slick('destroy')
        $slider.html($(cloneElements))
        $slider.slick(params)
        cloneElements = []
    })
})