import { errorPayment, errorRequestCall } from "./errorMessages";
import { phoneReplace } from "./replaces";

export const handlingPayment = (formDataEntries, formInput) => {
    const { name, phone, email, message, file } = Object.fromEntries(formDataEntries.entries());
    if(!errorPayment(name, phone, email, formInput)) return "error"
    return {
        name,
        phone: phoneReplace(phone),
        email,
        message,
        file
    }
}

export const handlingRequestCall = (formDataEntries, formInput) => {
    const { name, phone } = Object.fromEntries(formDataEntries.entries());
    if(!errorRequestCall(name, phone, formInput)) return "error"
    return {
        name,
        phone: phoneReplace(phone),
    }
}