$(function() {
	const $headerBtn = $('.js-header__btn');
	const $headerMenu = $('.js-header__menu');

	$headerBtn.on('click', function() {
		$(this).toggleClass('active');
		$headerMenu.toggleClass('active');
	});

	$(window).on('click', function(e) {
		if (!$headerMenu.is(e.target) && $headerMenu.has(e.target).length === 0 
			&& !$headerBtn.is(e.target) && $headerBtn.has(e.target).length === 0 ) {
			$headerBtn.removeClass('active');
			$headerMenu.removeClass('active');
		}
	})

})