export const TreesTemplate = (className, img, name ) => `
    <div class="configurator__timber js-timber ${className}" data-type="${name}">
        <div class="configurator__timber-img">
            <span class="configurator__timber-check">
                <img src="./img/svg/checkmark.svg" alt="Icon">
            </span>
            <img src="${img}" alt="Trees">
        </div>
        <p class="configurator__timber-name">${name}</p>
    </div>
`

export const ColorTemplate = (className, img, name, color) => `
    <div class="configurator__color configurator__color_1 js-color ${className}" data-type="${name}" data-window="${color}">
        <img src="${img}" alt="Color" title="${name}">
    </div>
`

export const AccessoryTemplate = (className, img, name, access) => `
    <div class="configurator__color configurator__color_2 js-accessory ${className}" data-type="${name}" data-accessory="${access}">
        <img src="${img}" alt="Color" title="${name}">
    </div>
`