import { inputsChange } from './components/inputsChange';
import { handlingPayment, handlingRequestCall } from "./components/handlers";

$(function() {
    $("[type='tel']").mask("+7 (999) 999-99-99", {
        completed: function() {
            $(this).removeClass('error')
        }
    });

    const $fileContent = $('.js-form__file-content');
    const $form = $('.js-form');
    const $fileInput = $('.js-file__input');
    let clone = null;
    let formValue = {};
    const fileContentTemplate = (text) => `
        <span>${text}</span>
        <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg" class="js-clear-file">
            <path d="M4.89775 4.10225C4.67808 3.88258 4.32193 3.88258 4.10226 4.10225C3.88259 4.32192 3.88259 4.67808 4.10226 4.89775L8.20451 9L4.10225 13.1023C3.88258 13.3219 3.88258 13.6781 4.10225 13.8978C4.32192 14.1174 4.67808 14.1174 4.89775 13.8978L9.00001 9.7955L13.1023 13.8978C13.3219 14.1174 13.6781 14.1174 13.8978 13.8978C14.1174 13.6781 14.1174 13.3219 13.8978 13.1023L9.7955 9L13.8977 4.89775C14.1174 4.67808 14.1174 4.32193 13.8977 4.10226C13.6781 3.88259 13.3219 3.88259 13.1023 4.10226L9.00001 8.20451L4.89775 4.10225Z" fill="white"/>
        </svg>
    `;
    
    $fileContent.on('click', function() {
        const $this = $(this)
        if(!$this.next().val()) return
        $this.next().val('')
        $this.html(clone)
    })

    $fileInput.on('change', function(e) {
        const target = e.target
        if(!target.files.length) return
        const $this = $(this)
        clone = $this.prev().children()
        $this.prev().html(fileContentTemplate(target.files[0].name.slice(0, 15) + '...'))
    })
    
    $('.js-form-success').on('click', function() {
        $.fancybox.close()
    })

    $form.on('submit', function(e) {
        e.preventDefault()
        const target = e.target
        const url = target.action
        const formInput = $(this).find('.js-form-input')
        const formBtn = $(this).find('.js-form-submit')
        const formDataEntries = new FormData(target)
        if(target.dataset.id === 'form-payment') formValue = handlingPayment(formDataEntries, formInput, target)
        if(target.dataset.id === 'form-request-call') formValue = handlingRequestCall(formDataEntries, formInput, target)
        if(formValue === "error") return inputsChange(formInput)
        
        formBtn.attr('disabled', true)
        formInput.addClass('disabled')

        fetch(url, {
            method: 'POST',
            body: JSON.stringify(formValue)
        })
        .then(function() {
            target.reset()
            formInput.blur()
            if(clone) {
                $('.js-form__file-content').html(clone)
                clone = null 
            }
            formInput.removeClass('disabled')
            formBtn.removeAttr('disabled')
            $.magnificPopup.close();
            $.magnificPopup.open({
                items: {
                    src: '#modal-success'
                  },
                type: 'inline',
                callbacks : {
                    open : function() {
                        setTimeout(()=> $.magnificPopup.close(), 3000)
                    }
                }
            });
        })
    })
})