let colorsData = []

export const initTrees = ($treesTemplate, data, TreesTemplate) => setTressImages($treesTemplate, 'Полукрывное', data, TreesTemplate)

export const setTressImages = ($treesTemplate, $type, data, TreesTemplate) => {
    const trees = data.filter(x=> x.name === $type)[0].trees
    const template = trees.map((item, i) => TreesTemplate(i === 0 ? 'active' : '', item["image-url"], item.name)).join('')
    $treesTemplate.html(template)
    return trees
}

export const initColors = ($colorTemplate, data, selecter, $attr, Template) => setColorImages($colorTemplate, 'Сосна', data, selecter, $attr, Template)

export const setColorImages = ($colorTemplate, $type, data, selecter, $attr, Template) => {
    if(data) colorsData = data
    const colors = colorsData.filter(x=> x.name === $type)[0][selecter]
    const template = colors.map((item, i) => Template(i === 0 ? 'active' : '', item["image-url"], item.name, item[$attr])).join('')
    $colorTemplate.html(template)
    return colors
}

