import { initTrees, initColors, setTressImages, setColorImages } from "./actions";
import { TreesTemplate, ColorTemplate, AccessoryTemplate } from "./templates";

$(function() {
    const $document = $(document);
    const $configurator = $('.js-configurator');
    const $coating = $('.js-coating');
    const $treesTemplate = $('.js-tress-template');
    const $colorTemplate = $('.js-color-template');
    const $accessoryTemplate = $('.js-accessory-template');
    const $configWindow = $('.js-config-window');
    const $configAccess = $('.js-config-access');
    const $timber = () => $('.js-timber');
    const $color = () => $('.js-color');
    const $accessory = () => $('.js-accessory');

    let materials = []
    let state = {
        coating: "Полукрывное",
        timber: "Сосна",
        color: "Цвет 1",
        accessory: "Серебро натуральное",
    }

    const getColorsValues = (type, data)=> {
        const color = setColorImages($colorTemplate, type, data, "colors", "window", ColorTemplate)
        const accessory = setColorImages($accessoryTemplate, type, data, "accessory", "access", AccessoryTemplate)
        return { color, accessory }
    }

    $.ajax($configurator.attr('data-url')).done(data=> {
        materials = data
        const trees = initTrees($treesTemplate, data, TreesTemplate)
        initColors($colorTemplate, trees, "colors", "window", ColorTemplate)
        initColors($accessoryTemplate, trees, "accessory", "access", AccessoryTemplate)
    })
    
    let currentCoating = null

    $coating.on('click', function() {
        const $this = $(this)
        const $dataType = $this.attr('data-type')
        if(currentCoating === $dataType) return
        toggleActive($coating, $this)
        const trees = setTressImages($treesTemplate, $dataType, materials, TreesTemplate)
        const { color, accessory } = getColorsValues(trees[0].name, trees)
        $configWindow.attr('src', color[0].window)
        $configAccess.attr('src', accessory[0].access)
        state = {
            coating: $dataType,
            timber: trees[0].name,
            color: color[0].name,
            accessory: accessory[0].name,
        }
        currentCoating = $dataType
    })

    let currentTimber = null

    $document.on('click', '.js-timber', function() {
        const $this = $(this)
        const $dataType = $this.attr('data-type')
        if(currentTimber === $dataType) return
        toggleActive($timber(), $this)
        const { color, accessory } = getColorsValues($dataType, false)
        $configWindow.attr('src', color[0].window)
        $configAccess.attr('src', accessory[0].access)
        state = {
            ...state,
            timber: $dataType,
            color: color[0].name,
            accessory: accessory[0].name,
        }
        currentTimber = $dataType
    })

    $document.on('click', '.js-color', function() {
        const $this = $(this)
        const $dataType = $this.attr('data-type')
        const $dataWindow = $this.attr('data-window')
        toggleActive($color(), $this)
        $configWindow.attr('src', $dataWindow)
        state = {
            ...state,
            color: $dataType
        }
    })

    $document.on('click', '.js-accessory', function() {
        const $this = $(this)
        const $dataType = $this.attr('data-type')
        const $dataAccessory = $this.attr('data-accessory')
        toggleActive($accessory(), $this)
        $configAccess.attr("src", $dataAccessory)
        state = {
            ...state,
            accessory: $dataType
        }
    })
})

function toggleActive($all, $this) {
    $all.removeClass('active');
    $this.addClass('active');
}